.dpmsg-Icon {
  display: inline-block;
  flex-shrink: 0;
  height: var(--height-icon);
  vertical-align: top;
  width: var(--width-icon);
}

.dpmsg-iconOnly {
  background-color: transparent;
  border: none;
  display: inline-block;
  outline: none;

  &:hover,
  &:focus {
    outline: none;
  }
}

.dpmsg-Icon--Round {
  border-radius: 50%;
  overflow: hidden;
}

.dpmsg-Icon img {
  height: auto;
  max-height: 100%;
  max-width: 100%;
}

[class^="dpmsg-Icon"] {
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
}

.dpmsg-IconArrow {
  cursor: pointer;
  height: 16px;
  width: 16px;
}

