.dpmsg-IconArrow {

  &.iconArrow--right {
    transform: rotate(-90deg);
  }

  &.iconArrow--left {
    transform: rotate(90deg);
  }

  &.iconArrow--top {
    transform: rotate(180deg);
  }
}

.dpmsg-IconArrow.is-open {
  transform: rotate(180deg);
  transition: transform var(--animation-speed);
}

.dpmsg-IconBadge {
  align-items: center;
  background: linear-gradient(to top, var(--red) 50%, color-mod(var(--red) blackness(20%)));
  border-radius: 50%;
  box-shadow: inset 2px 3px 5px 1px rgba(46, 51, 73, 0.2);
  color: var(--white);
  display: flex;
  font-size: 17px;
  height: 1.7em;
  justify-content: center;
  line-height: 1;
  width: 1.7em;

  &.is-inactive {
    background: linear-gradient(to top, var(--grey-400), var(--grey-400));
  }
}

.dpmsg-IconBadgeWrap {
  position: relative;
}

.dpmsg-IconBadgeWrap .dpmsg-IconBadge {
  position: absolute;
  top: -0.7em;
  top: -0.7em;
}

.dpmsg-IconBadgeWrap.is-left .dpmsg-IconBadge {
  left: -0.85em;
}

.dpmsg-IconBadgeWrap.is-right .dpmsg-IconBadge {
  right: -0.85em;
}

