.dp-bg--default {
  color: #2b2f2f;
  background-color: #ffffff;
}

.dp-bg--success {
  color: #ffffff;
  background-color: #47bf61;
}

.dp-bg--info {
  color: #ffffff;
  background-color: #4696dc;
}

.dp-bg--warning {
  color: #ffffff;
  background-color: #fbb03b;
}

.dp-bg--danger {
  color: #ffffff;
  background-color: #ed4954;
}

