/******************************
    Font families
*******************************/
/******************************
    Font weights
*******************************/
/******************************
    Font sizes
*******************************/
.dp-code {
  font-family: "Courier10 BT", Courier, monospace;
}

/******************************
    Icon sizes
*******************************/
.dp-icon--xs {
  font-size: 9px !important;
}

.dp-icon--s {
  font-size: 12px !important;
}

.dp-icon--m {
  font-size: 16px !important;
}

.dp-icon--l {
  font-size: 18px !important;
}

.dp-icon--xl {
  font-size: 20px !important;
}

.dp-text-center {
  text-align: center !important;
}

.dp-text-right {
  text-align: right !important;
}

.dp-text-left {
  text-align: left !important;
}

.dp-container {
  padding: 12px;
}

.dp-section {
  padding: 12px 0;
}

.dp-section--hidden {
  display: none;
}

.dp-alert {
  border-radius: 3px;
  display: flex;
  padding: 6px;
  align-items: flex-start;
  justify-content: space-between;
  font-family: "Open Sans", sans-serif;
  font-size: 13px;
  font-weight: 400;
}

.dp-alert__icon {
  margin: 4px 12px 0 3px;
  font-size: 13px;
}

.dp-alert__close {
  margin: 0 3px 0 12px;
  cursor: pointer;
  font-size: 13px;
}

.dp-alert__content {
  flex-grow: 2;
  word-wrap: break-word;
  overflow: hidden;
}

.dp-highlighted {
  font-style: normal;
  background-color: #fffcc2;
}

.dp-colour-tails-primary {
  color: #fbb03b;
}

.dp-colour-night-sky {
  color: #28323c;
}

.dp-colour-morning-sky {
